import React from 'react';
import { Link } from 'react-router-dom';
import Icon from 'react-fontawesome';
import FacebookLogin from 'react-facebook-login';

const responseFacebook = (response) => {
  console.log(response);
}

export default class SocialSign extends React.Component {
  render () {
    return (
      <div  className="top-bar__social-sign social-sign">
        <p className="social-sign__text-caption">Войти с помощью:</p>
        <FacebookLogin
          appId="338314913255947"
          autoLoad={false}
          fields="name,email,picture"
          cssClass="social-sign__btn social-sign__btn--facebook"
          icon="fa-facebook"
          textButton=""
          tag="button"
          callback={responseFacebook}>
        </FacebookLogin>
        <Link to="" className="social-sign__btn social-sign__btn--vk">
          <Icon
            name="vk"
            tag="i"/>
        </Link>
      </div>
    );
  }
}


